
import Header from "@/components/common/Header";
import img from "@/assets/images/block-chain-img.png";
import { timeTodate } from "@/assets/js/tools";
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

export default {
name: "",
props: [""],
data() {
return {
TitleObjData: {
titleContent: "",
showLeftBack: true,
showRightMore: false
},
conObj: {},
chainCertificate: {
backgroundImage: "url(" + img + ")"
}
};
},

components: {
Header
},
filters: {
formateDate(val) {
return timeTodate(val);
}
},
computed: {
scenceGiftsContent() {
return {
height: document.documentElement.clientHeight - 45 + "px"
};
}
},

beforeMount() {},

mounted() {
this.setTitle();
},

methods: {
setTitle() {
this.TitleObjData.titleContent = this.$route.query.title;
this.conObj = JSON.parse(this.$store.state.blockChainInfo);
}
},

watch: {}
};



